<template>
		<div class="page-body">
			<ArticleListItem :key="post.id" v-bind:post="post" :show-comments="true"/>
		</div>
</template>

<script>
import ArticleListItem from '@/components/ArticleListItem';
export default {
	name: 'PageBody',
	components: {ArticleListItem},
	created(){
		//console.log(this.$route.params.pagename);
		this.getPage();
	},
	methods:{
		getPage(){
			this.fetchPageContent(this.$route.params.pagename).then(v=>this.post=v);
		}
	},
	data(){
		return {post:[]}
	},
	watch:{
		'$route':['getPage']
	}
}
</script>

<style>
</style>


