<template>
		<div class="page-body">
			<ArticleListItem :key="post.id" v-bind:post="post" :show-comments="true"/>
		</div>
</template>

<script>
import ArticleListItem from '@/components/ArticleListItem';
export default {
	name: 'ArticleBody',
	components: {ArticleListItem},
	created(){
		this.fetchArticleContent(this.$route.params.id).then(v=>this.post=v);
	},
	data(){
		return {post:[]}
	}
}
</script>

<style>
</style>


