<template>
	<div id="app">
		<div class="bg"></div>
		<wzheader/>
		<transition name="slide-fade">
			<router-view/>
		</transition>
	</div>
</template>

<script>
import wzheader from '@/components/wzheader';
export default {
	name: 'App',
	components: {wzheader}
}
</script>

<style>
@import "@mdi/font/css/materialdesignicons.min.css";
@import "highlightjs/styles/railscasts";
* {
	font-family: "Microsoft YaHei","PingFang SC","Times New Roman","sans-serif";
	box-sizing: border-box;
  }
  h1,h2,h3,h4,h5,h6,h7,b,strong{
	  color:#222;
  }
  .mdi{
	  display: inline-block;
	  font-family:"Material Design Icons","Microsoft YaHei Light";
	  font-size: inherit;
	  text-rendering: auto;
	  line-height: inherit;
	  -webkit-font-smoothing: antialiased;
  }
  code,code *{
	font-family:  Consolas, "Microsoft YaHei Light";
	font-size: 14px;
  }
  a {
	text-decoration: none;
	color: inherit;
  } 
  body{
	  margin:0;
		overflow-x:hidden;
  }
	.bg{
		position:fixed;
		width:100vw;
		height:100vh;
		top:0;
		left:0;
		z-index:-1;
		background-image:url('assets/bg.jpg');
		background-size:cover;
	}
	#app{
		display:flex;
		width:100vw;
		position: absolute;
		
		flex-direction: column;
		align-items: center;
	}
	.page-body{
		width:100%;
		display:flex;
		flex-direction: column;
		align-items: center;
	}
::-webkit-scrollbar {
  width: 15px;
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
::-webkit-scrollbar-thumb:active {
  background-color: rgba(0, 0, 0, 0.6);
}
.slide-fade-enter-active {
  transition: all .3s ease;
	position:absolute;
	top:82px;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(10px);
  opacity: 0;
}
</style>


