<template>
		<div class="page-body">
			
			<ArticleListItem v-for="post in posts" :key="post.id" v-bind:post="post"/>
			
		</div>
</template>

<script>
import ArticleListItem from '@/components/ArticleListItem';
export default {
	name: 'IndexBody',
	components: {ArticleListItem},
	created(){
		this.fetchArticleList().then(v=>this.posts=v);
	},
	data(){
		return {posts:[]}
	}
}
</script>




