import marked,{Renderer} from 'marked';
var highlightjs=require('highlight.js');

const renderer = new Renderer();
renderer.code = (code, language) => {
  const validLang = !!(language && highlightjs.getLanguage(language));
  const highlighted = validLang ? highlightjs.highlight(language, code).value : code;
  return `<pre><code class="hljs ${language}">${highlighted}</code></pre>`;
};

marked.setOptions({ renderer });


export default {
	install:function(Vue,opitions){
		var conf={
			'apiurl':'https://api.github.com/repos/Archeb/archeb.github.io'
		}
		Vue.prototype.fetchArticleList=function(){
			return fetch(conf.apiurl + "/issues?creator=Archeb&labels=article",{headers:{'Accept':'application/vnd.github.symmetra-preview+json'}})
			.then(b=>b.json())
			.then(b=>{
				b.forEach(bv=>{
					bv.body=marked(bv.body);
				});
				return b;
			});
		}
		Vue.prototype.fetchArticleContent=function(number){
			return fetch(conf.apiurl + "/issues/" + number,{headers:{'Accept':'application/vnd.github.symmetra-preview+json'}})
			.then(b=>b.json())
			.then(b=>{
				b.body=marked(b.body);
				b.showComments=true;
				return b;
			});
		}
		Vue.prototype.fetchPageContent=function(pagename){
			return fetch(conf.apiurl + "/issues?creator=Archeb&labels=" + pagename,{headers:{'Accept':'application/vnd.github.symmetra-preview+json'}})
			.then(b=>b.json())
			.then(b=>{
				b=b[0];
				if(b){
					b.body=marked(b.body);
					b.showComments=true;
					return b;
				}else{
					b={
						id:0,
						body:"<p>您所请求的页面并不存在</p>"
					};
					return b;
				}
			});
		}
	}
}


