<template>
	<div class="article-item">
		<router-link :to="{ name: 'article', params: { id: post.number }}">
			<div class="article-title">
				<div class="title-content" v-html="post.title"></div>
				<div class="time-content" v-html="post.created_at"></div>
			</div>
			<div class="article-body" v-html="post.body"></div>
		</router-link>
		<div v-if="post.showComments" class="article-comment">
			<a :href=post.html_url>请到原文issue下评论，点击进入</a>
		</div>
	</div>
</template>

<script>
export default {
	name: 'ArticleListItem',
	props:['post']
}
</script>

<style lang="less">

.article-item{
	margin:15px 0 0 0;
	max-width:650px;
	padding:10px 0 0;
	width:100%;
	background-color:rgba(255,255,255,0.7);
	transition:all 0.5s;
	box-shadow:1px 1px 15px rgba(0,0,0,0.3);
	&:hover{
		transform:translate(0,-2px);
		background-color:rgba(255,255,255,0.75);
		box-shadow:1px 1px 15px rgba(0,0,0,0.3),1px 1px 15px rgba(0,0,0,0.3);
	}
	.article-title{
		margin: 10px 30px;
		display: flex;
		justify-content: space-between;
		
		.title-content{
			color:#000;
			font-size:1.3rem;
			border-bottom:1px dashed #999;
			padding-bottom:2px;
		}
		.time-content{
			margin-top:5px;
			flex-shrink:0;
			color:#666;
			font-size:0.8rem;
		}
		
		
	}
	.article-body{
		padding:0px 30px;
		color:#555;
		width:100%;
		font-size:0.9rem;
		& *{
			max-width:100%;
		}
	}
	.article-comment{
		margin-top: 20px;
		padding: 20px 30px;
		color: #555;
		background-color: rgba(255,255,255,0.5);
		width: 100%;
		font-size: 0.9rem;
		&>*{
			max-width:100%;
		}
		a{
			color:#007dbb;
		}
	}
}


</style>


