<template>
	<div class="wzheader">
		<div class="top-group">
			<div class="group">
				<router-link to="/" class="header-item title active">
					贫困的蚊子
				</router-link>
				<router-link to="/page/about" class="header-item link">
					关于
				</router-link>
				<router-link to="/page/links" class="header-item link">
					链接
				</router-link>
			</div>
			<div class="group">
				<a class="header-item link logo">
					<img src="https://qwq.moe/img/avatar.jpg">
				</a>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'wzheader'
}
</script>

<style lang="less">
.wzheader{
	margin:35px 0 0 0;
	height:47px;
	max-width:650px;
	width:100%;
	box-shadow:1px 1px 15px rgba(0,0,0,0.3);
	font-size:1.05rem;
	.top-group{
		width:100%;
		display:flex;
		background-color:rgba(0,0,0,0.3);
		justify-content: space-between;
	}
	.group{
		display:flex;
		align-items: center;
	}
	.header-item{
		margin-left:13px;
		margin-right:13px;
		height: 100%;
		display: flex;
		align-items: center;
		flex-direction: row;
		border-bottom:1px solid transparent;
		transition:all 0.5s;
		color:#d9d9d9;
		font-size:0.95rem;
		&:hover{
			color:#fff;
		}
	}
	.title{
		padding:8px 20px;
		margin:0 7px 0 0;
		font-size:1.4rem;
		font-family:'Microsoft Yahei Light'!important;
	}
	.active{
		color:#f1f1f1;
		background-color:rgba(0,0,0,0.3);
	}
	.logo{
		border-bottom:1px solid transparent!important;
		img{
			width:32px;
			height:32px;
			border-radius:50%;
		}
	}
}

</style>


